import React, { Component } from "react";
import { ProductConsumer } from "../../context";
import { Link } from "react-router-dom";

export class NavBar extends Component {
  render() {
    return (
      <ProductConsumer>
        {(value) => {
          return (
            <nav className="navbar navbar-expand-lg navbar-light bg-light text-uppercase">
              <Link to="/nike" className="navbar-brand">
                nike
              </Link>
              <button
                className="navbar-toggler"
                type="button"
                data-toggle="collapse"
                data-target="#navbarSupportedContent"
                aria-controls="navbarSupportedContent"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>

              <div
                className="collapse navbar-collapse"
                id="navbarSupportedContent"
              >
                <ul className="navbar-nav mr-auto">
                  <li className="nav-item dropdown">
                    <a
                      className="nav-link dropdown-toggle"
                      href="#"
                      id="navbarDropdown"
                      role="button"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    >
                      men
                    </a>
                    <div
                      className="dropdown-menu"
                      aria-labelledby="navbarDropdown"
                    >
                      <Link
                        to="/nike"
                        className="dropdown-item"
                        onClick={() => value.filterProducts("men", "shoes")}
                      >
                        shoes
                      </Link>{" "}
                      <div className="dropdown-divider"></div>
                      <Link
                        to="/nike"
                        className="dropdown-item"
                        onClick={() => value.filterProducts("men", "shirts")}
                      >
                        shirts
                      </Link>
                      <div className="dropdown-divider"></div>
                      <Link
                        to="/nike"
                        className="dropdown-item"
                        onClick={() => value.filterProducts("men", "shorts")}
                      >
                        shorts
                      </Link>
                    </div>
                  </li>
                  <li className="nav-item dropdown">
                    <a
                      className="nav-link dropdown-toggle"
                      id="navbarDropdown"
                      role="button"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    >
                      women
                    </a>
                    <div
                      className="dropdown-menu"
                      aria-labelledby="navbarDropdown"
                    >
                      <Link
                        to="/nike"
                        className="dropdown-item"
                        onClick={() => value.filterProducts("women", "shoes")}
                      >
                        shoes
                      </Link>{" "}
                      <div className="dropdown-divider"></div>
                      <Link
                        to="/nike"
                        className="dropdown-item"
                        onClick={() => value.filterProducts("women", "shirts")}
                      >
                        shirts
                      </Link>
                      <div className="dropdown-divider"></div>
                      <Link
                        to="/nike"
                        className="dropdown-item"
                        onClick={() => value.filterProducts("women", "shorts")}
                      >
                        shorts
                      </Link>
                    </div>
                  </li>
                  <li className="nav-item dropdown">
                    <a
                      className="nav-link dropdown-toggle"
                      id="navbarDropdown"
                      role="button"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    >
                      kids
                    </a>
                    <div
                      className="dropdown-menu"
                      aria-labelledby="navbarDropdown"
                    >
                      <Link
                        to="/nike"
                        className="dropdown-item"
                        onClick={() => value.filterProducts("kids", "shoes")}
                      >
                        shoes
                      </Link>{" "}
                      <div className="dropdown-divider"></div>
                      <Link
                        to="/nike"
                        className="dropdown-item"
                        onClick={() => value.filterProducts("kids", "shirts")}
                      >
                        shirts
                      </Link>
                      <div className="dropdown-divider"></div>
                      <Link
                        to="/nike"
                        className="dropdown-item"
                        onClick={() => value.filterProducts("kids", "shorts")}
                      >
                        shorts
                      </Link>
                    </div>
                  </li>
                  <li className="nav-item">
                    <Link to="/nike/cart" className="nav-link">
                      cart
                    </Link>
                  </li>
                </ul>
                <form className="form-inline my-2 my-lg-0">
                  <input
                    className="form-control mr-sm-2"
                    type="search"
                    placeholder="Search"
                    aria-label="Search"
                    onChange={value.filterProductsBySearch}
                    name="search"
                  />
                </form>
              </div>
            </nav>
          );
        }}
      </ProductConsumer>
    );
  }
}

export default NavBar;
